import { Component,OnInit } from '@angular/core';
import { CookieService, FeatureAuthService} from './../../shared/services/index';

@Component({
  selector: 'og-builder',
  template: `
    <router-outlet></router-outlet>
  `,
})

export class BuilderParentComponent implements OnInit{
  constructor(
    private _cookieService : CookieService,
    private _featureAuthService : FeatureAuthService
  ) {

  }
  ngOnInit(){
    if(this._cookieService.readCookie('storage'))
      this._featureAuthService.getAllFeatureAccess();
  }
}



