import { Component, OnInit } from '@angular/core';
import { SubDomainService } from '../../../shared/services/index';
import {environment} from '../../../../environments/environment';
import {ActivatedRoute} from '@angular/router';
declare var jQuery: any;
declare var window: any;

@Component({
  selector: 'og-sample-code',
  template: `
    <div id="main-profile">
        <iframe
        [class.full-page]="pageType==='full-page'"
        [class.small-page]="pageType==='small-page'"
        [src]="src | safeUrl" align="middle">
        </iframe>
    </div>`,
  styles: [`
    .responsive-menu {
        background: rgba(0, 0, 0, 0.45);
        color: #fff;
        width: 320px;
        text-align: center;
        padding: 6px 6px;
        position: absolute;
        z-index: 999;
        margin: 0 auto;
        margin-left: auto;
        margin-right: auto;
        left: -3px;
        right: 0;
        top: 10px;
        font-size: 13px;
    }

    .responsive-menu a {
        color: #fff;
        padding: 0 5px;
            float: left;
    }

    .responsive-menu a span {
        margin-top: -20px;
        margin-left: 13px;
        float: right;
        margin-top: 3px;
        color: rgba(255, 255, 255, 0.31);
    }

    iframe {
        width: 100%;
        height: 100%;
        position: absolute;
        margin-left: auto;
        margin-right: auto;
        left: 0;
        right: 0;
        top: 0px;
        border: none;
    }
    .active-view i{
        color: #fb545b;
    }
    .responsive-menu span.title {
        margin-right: 26px;
        float: left;
        margin-top: 7px;
        margin-left: 26px;
    }

    .responsive-menu div {
        float: left;
        margin-top: 4px;
    }

    .full-page {
        width: 100%;
        height: 100%;
    }

    .small-page {
        width: 375px;
        margin: 0 auto;
        height: 50%;
        border: 1px solid #dcdddf;
    }
`]
})

export class SampleCodeComponent implements OnInit {
  src: string;
  pageType: string = 'full-page';
  private sub: any;
  constructor(private subDomainService: SubDomainService, private route: ActivatedRoute) {
  }

  ngOnInit() {
    this.sub = this.route.params.subscribe(params => {
      let type = params['type'];
      if (type)
        this.pageType = type;
    });

    this.src = environment.PROTOCOL + this.subDomainService.subDomain.sub_domain + '.' + environment.APP_EXTENSION + '/preview/previewFrame';
    let template = localStorage.getItem('template');
    if (template) {
      let app = JSON.parse(template);
      jQuery('meta[name=description]').attr('content', app.description);
      window.parent.document.title = app.title;
      jQuery('#favicon').attr('href', app.favicon);
    }
  }
}



